var path = require("path");
var fs = require("fs");
var distDir = path.join(process.cwd(), "dist");
export function EnableHotReload(window) {
    try {
        var files = fs.readdirSync(distDir);
        var stats = [];
        files.forEach(function (file) {
            var fileDir = path.join(distDir, file);
            fs.watch(fileDir, function (event, filename) {
                if (filename) {
                    window.reload();
                }
            });
        });
    }
    catch (err) {
        throw new Error(err);
    }
}
